import React from  "react" 
import {useStaticQuery,graphql} from "gatsby"
import {Typography,createStyles,makeStyles} from '@material-ui/core'
import {IconLink} from './IconLink'
import {Doughnut} from "react-chartjs-2"

const useStyles=makeStyles(()=>
  createStyles({
    flex:{
      display:'flex',
      alignItems:'center',
      justifyContent:'center',
    },
    typo:{
      margin:'5px',
    },
  }),
);

export const GitChart:React.FC<{}>=()=>{
    const tempData= useStaticQuery(graphql`
    query {
      githubData {
        data{
          viewer {
            name
            avatarUrl
            isHireable
            resourcePath
            repositories {
              totalCount
              nodes {
                name
                description
                homepageUrl
                forkCount
                createdAt
                updatedAt
                resourcePath
                languages {
                  totalSize
                  totalCount
                  edges {
                    size
                    node {
                      name
                      color
                    }
                  }
                }
    
                stargazers {
                  totalCount
              
                }
              }
            }
          }
        }
      }
    }
    `)
    // totalCount
  const totalRepo=tempData.githubData.data.viewer.repositories.totalCount
  const langName1=[]
  const langSize1=[]
  for(let i=0;i<totalRepo;i++){
    let langEachRepo=tempData.githubData.data.viewer.repositories.nodes[i].languages.totalCount
    for(let j=0;j<langEachRepo;j++){
      langName1.push(tempData.githubData.data.viewer.repositories.nodes[i].languages.edges[j].node.name)
      langSize1.push(tempData.githubData.data.viewer.repositories.nodes[i].languages.edges[j].size)
    }
  }
  console.log(langName1);
  console.log(langSize1);
  // [key,value]
  var langSet = [];
  for(var i=0;i<langSize1.length;i++){
  
    langSet.push([langName1[i],langSize1[i]]);
    
  }
  console.log(langSet);
  // 
  var sum={};
  for(var idi=0;idi<langSet.length;idi++){
     var key=langSet[idi][0];     // i++keylangSet[i]
    if(key in sum){            // sumkey
      sum[key]+=langSet[idi][1]; // langSet[i]
    }else{
      sum[key]=langSet[idi][1];  //
    }
  }
  console.log(sum);
  delete sum['Mask']
  delete sum['HLSL']
  delete sum['Mathematica']
  delete sum['ShaderLab']
  
  const label1=[];
  const data1=[];
  for(var key2 in sum){
    label1.push(key2);
    data1.push(sum[key2])
  }
  console.log(label1);
  console.log(data1);
    const data={
      labels:label1,
      datasets:[{
        backgroundColor:["#178600","#2B7489","#e34c26","#563D7C","#f1e05a"],
        data:data1,
      },],
    };
  const classes=useStyles();
  
  return(
    <div>
      <div className={classes.flex}>
        <Typography 
        className={classes.typo} 
        color="textSecondary" 
        variant="h6" 
        gutterBottom>
         In GitHub
        </Typography>
        <IconLink title="github" url="https://github.com/yumi9502" />
      </div>
    <Doughnut data={data} />
    </div>
  )
  }
  //export default LangData