import React from  "react" 
import {Typography,makeStyles} from '@material-ui/core'

const useStyle=makeStyles({
    root:{
        paddingLeft:'10px'
    }
});

export const Skill:React.FC<{}>=()=>{
    const classes=useStyle();
    return(
        <div className={classes.root}>
            <Typography>
                <h2></h2>
                <p><br/>
                UnityJava,JavaScript<br />
                C#</p>
            </Typography>
        </div>
    )
}